*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 1em;
  font-family: 'Courier New', Courier, monospace;
  overflow: hidden;

}

.body{
  display: flex;
  align-items: center;
  justify-content: center;
}

.bankapp{
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: flex-start;

  margin-top: 50px;  
  background-color: rgb(0,0,0);  
  border-radius: 25px;

  /* REFERENCE 220px 110x */
  height: 550px;
  width: 275px;
}

.bankrupt{
  margin-top: 50px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.5em;
  font-weight: bold;
  color: white;
}

.italic{
  font-style: italic;
}

.circle{
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(150deg, #4169E1, #7B68EE, white);
  height: 200px;
  width: 200px;
  border-radius: 100px;
}

.innercircle{
  background-color: black;
  height: 150px;
  width: 150px;
  border-radius: 75px;
}

.loginform{
display: flex;
flex-direction: column;
align-items: flex-end;
margin-top: 50px;
font-size: 0.8em;
}

.login{
  margin-top: 10px;
  color: white;
  font-size: 1em;
}

.logininput{
  color: black;
  margin-top: 20px;
  font-weight: 100;
  margin-left: 20px;
  width: 150px;
}

.loginbutton{
  color: black;
  margin: 30px 10px;
  font-size: 1em;
  width: 70px;
  border-radius: 5px;
  font-weight: 700;
}

.buttonSubmitSignUp{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}