*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 1em;
    font-family: 'Courier New', Courier, monospace;
    overflow: hidden;
    color: white;
    border: 2px solid white;
  }
  
  .body{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .bankapp{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: flex-start;
  
    margin-top: 50px;  
    background-color: rgb(0,0,0);  
    border-radius: 25px;
  
    /* REFERENCE 220px 110x */
    height: 550px;
    width: 275px;
  }

  .profileform{
    display: flex;
    margin-top: 15px;
    display: flex;
    width: inherit;
    border: 5px solid red;
  }

  .photo{
    background-color: blue;
    border-radius: 40px;
    height: 80px;
    width: 80px;
    border: 2px solid orange;
  }

  .greetings{
    display: flex;
    flex-direction: column;
    flex-grow: 1;    
  }

  .greet{
    text-align: left;
    width: 100%;
    border: 2px solid green;
  }

  .name{
    display: flex;
    flex-grow: 1;
    align-items: flex-end;    
  }

  .balanceform{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    height: 150px;
    width: 230px;
    border-radius: 25px;
  }

  .displayguide{
    display: flex;
  }

  .balanceimage{
    flex-grow: 1;
    margin-top: 15px;
  }

  .balancetext{
    margin-top: 15px;
    font-size: 0.8em;
    width: 75%;
  }

  .currentbalance{
    text-align: left;
    margin-left: 20px;

  }

  .pesos{
    margin-left: 20px;
  }

  .creditcardnumber{
    margin-top: 50px;
  }

  .billsform{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    width: 230px;
  } 

  .billsitems{
    display: flex;
  }

  .bills{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8em;
    width: 57.5px;
    height: 50.5px;
    background-color: green;
    padding: 10px;
    margin: 3px;
    border-radius: 10px;
  }

  .bills:hover{
    background-color: red;
  }

  .onTransaction{
    margin-top: 20px;

    text-align: left;
  }

  .button{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin-top: 10px;
    color: black;
  }

  .inputText{
    color: black;
  }